@import url(https://fonts.googleapis.com/css2?family=Radio+Canada:wght@300;400;700&display=swap);
*{
    margin: 0;
    padding: 0;
    font-family: 'Radio Canada', sans-serif;
}

.app{
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: #dadbd3;
}

.app-body{
    display: flex;
    background-color: #ededed;
    height: 90vh;
    width: 90vw;
    box-shadow: -10px 2px 20px -6px rgba(0, 0, 0, 0.75);
}
.chat{
    display: flex;
    flex-direction: column;
    flex: 0.65 1;
}

.chat-header{
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat-header-info{
    flex: 1 1;
    padding-left: 20px;
}

.chat-header-info > h3{
    margin-bottom: 3px;
    font-weight: 500;
}

.chat-header-info > p{
    color: gray;
}

.chat-body{
    flex: 1 1;
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow-y: scroll;

}

.chat-message{
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 10px;
    background-color: #fff;
    margin-bottom: 30px;
}

.chat-timestamp{
    margin-left: 10px;
    font-size: xx-small;
}

.chat-name{
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}

.chat-rec{
    margin-left: auto;
    background-color: #dcf8c6;
}
.sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.35 1;
}

.sidebar-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar-header-right{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}

.sidebar-header-right > .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}

.sidebar-search{
    display:flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}

.sidebar-search-container{
    display: flex;
    align-items: center;
    background-color: #fff;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar-search-container > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}

.sidebar-search-container > input{
    border: none;
    outline: none;
    margin-left: 10px;
    width: 100%;
}

.sidebar-chats{
    flex: 1 1;
    background-color: white;
    overflow: scroll;
    overflow-x: hidden;
}
.sidebar-chat{
    display: flex;
    padding: 20px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
}

.sidebar-chat:hover{
    background-color: #ebebeb;
}

.sidebar-chat-info > h2{
    font-size: 16px;
    margin-bottom: 8px;
}

.sidebar-chat-info{
    margin-left: 15px;
}
